import React from 'react';
import {render} from 'react-dom';
import styles from '../stylesheet.css';


//style sheet
const stylesOBJ = { 
        button : {
            fontFamily: 'Source Sans Pro, sans-serif',
            backgroundColor: 'white',
            fontSize: 15,
            border : "1px solid #C0C0C0" ,
            borderRadius: 4,
            padding: "10px 5px",
            width: "15%"
        },

        hover: {
            border : '1px solid black',
            borderRadius: 4
        },

        names: {
            padding: 5,
            fontSize: 25,
            width: '40%'
        },

        narrator :{
            padding: 1,
            fontSize: 20,
            width: '18%'
        },

        dead :{
            backgroundColor: '#AE0D11'
        },

        impact : {
            fontFamily: 'Impact',
            color : "black",
            fontSize : 35
        },

        red :{
            color:'#AE0D11'
        }

};

class App extends React.Component {  
    render () {
     
        // the components
        return( 
            <div>
                <button className={styles.button}> 
                    NEW GAME
                </button >
                

                <button style = { stylesOBJ.button }> 
                    JOIN GAME
                </button >
            </div>
        );
    }
    
}



render(<App/>, document.getElementById('app'));


